import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '@/store'
import Index from "@/views/Index";
import Home from "@/views/home/Home";
import Archives from "@/views/archives/Archives";
import Showrrel from "@/views/showrrel/Showrrel";
import Tag from "@/views/tag/Tag";
import Contact from "@/views/contact/Contact";
import Login from "@/views/Login";

Vue.use(VueRouter)

const routes = [
	{
		path: '/login',
		component: Login,
		meta: {
			title: 'Login'
		}
	},
	{
		path: '/',
		component: Index,
		redirect: '/home',
		children: [
			{
				path: '/',
				name: 'home',
				component: Home,
				meta: {
					title: 'Home'
				}
			},
			{
				path: '/showreel',
				name: 'showreels',
				component: Archives,
				meta: {
					title: 'Showreel'
				}
			},
			{
				path: '/showreel/:id',
				name: 'showreel',
				component: Showrrel,
				meta: {
					title: 'Showreel'
				}
			},
			{
				path: '/tag/:name',
				name: 'tag',
				component: Tag,
				meta: {
					title: 'Tag'
				}
			},
			{
				path: '/contact',
				name: 'contact',
				component: Contact,
				meta: {
					title: 'Contact'
				}
			}
		]
	}
]

const router = new VueRouter({
	mode: 'history',
	base: process.env.BASE_URL,
	routes
})

//Mounting route guard 
router.beforeEach((to, from, next) => {
	if (to.meta.title) {
		if (store.state.siteInfo.webTitleSuffix) {
			document.title = to.meta.title + store.state.siteInfo.webTitleSuffix
		} else {
			document.title = to.meta.title
		}
	}
	next()
})

export default router
