<script>
import { getBlogById } from "@/api/blog";
import { mapState } from "vuex";
import { SET_FOCUS_MODE, SET_IS_BLOG_RENDER_COMPLETE } from "@/store/mutations-types";

export default {
  name: "Blog",
  data() {
    return {
      blog: {},
      bigFontSize: true,
      playerVars: {
        autoplay: 1,
      },
    };
  },
  computed: {
    blogId() {
      return parseInt(this.$route.params.id);
    },
    ...mapState(["siteInfo", "focusMode"]),
  },
  beforeRouteEnter(to, from, next) {
    //Before the route to the blog Showrrel page, the rendering of the Showrrel should be set to false.
    next((vm) => {
      // Before the beforeRouteEnter hook is executed, the component instance has not been created
      // vm is an instance of the current component, you can use vm as this in the next method
      vm.$store.commit(SET_IS_BLOG_RENDER_COMPLETE, false);
    });
  },
  beforeRouteLeave(to, from, next) {
    this.$store.commit(SET_FOCUS_MODE, false);
    // When the Showrrel page is routed to other pages, the current component is destroyed while destroying the TOCBOT instance.
    // Otherwise, TOCBOT has been listening to the page scrolling event, and the anchor point of the Showrrel page has no longer existence, the report "Uncaught Typeerror: Cannot Read Property 'ClassName' of Null"
    tocbot.destroy();
    next();
  },
  beforeRouteUpdate(to, from, next) {
    // Generally there are two situations that trigger this hook
    // ①The current Showrrel page jumps to other Showrrel pages
    // ②When you click the directory jump anchor, the routing hash value will change, causing the current page to be reloaded. This situation is undesirable
    // Determine whether the path has changed in the route beforeRouteUpdate
    // If you jump to another page, to.path!==from.path will let go next()
    // If it is a jump anchor point, path will not change, hash will change, to.path===from.path, to.hash!==from.path If the route jump is not allowed, the anchor point can be jumped normally change
    if (to.path !== from.path) {
      this.$store.commit(SET_FOCUS_MODE, false);
      //When the current component is routed to other blog Showrrels, you must re-acquire your Showrrel.
      this.getBlog(to.params.id);
      //As long as the routing path changes, and stay in the current blog component, set the rendering of the Showrrel to False
      this.$store.commit(SET_IS_BLOG_RENDER_COMPLETE, false);
      next();
    }
  },
  created() {
    this.getBlog();
  },
  methods: {
    getBlog(id = this.blogId) {
      //Password-protected Showrrels need to send the Token saved in localStorage after the password verification is passed
      const blogToken = window.localStorage.getItem(`blog${id}`);
      //If so, send the blogger identity token
      const adminToken = window.localStorage.getItem("adminToken");
      const token = adminToken ? adminToken : blogToken ? blogToken : "";
      getBlogById(token, id)
        .then((res) => {
          if (res.code === 200) {
            this.blog = res.data;
            document.title = this.blog.title + this.siteInfo.webTitleSuffix;
            //Rendering code block style after V-HTML rendering
            this.$nextTick(() => {
              Prism.highlightAll();
              //Set the Showrrel to True
              this.$store.commit(SET_IS_BLOG_RENDER_COMPLETE, true);
            });
          } else {
            this.msgError(res.msg);
          }
        })
        .catch(() => {
          this.msgError("Request failed");
        });
    },
    changeFocusMode() {
      this.$store.commit(SET_FOCUS_MODE, !this.focusMode);
    },
  },
};
</script>

