<template>
  <div>
    <div class="ui top segment" style="text-align: center">
      <h2 class="m-text-500">Label {{ tagName }} Under the Showrrel</h2>
    </div>
    <ShowrrelList :getBlogList="getBlogList" :blogList="blogList" :totalPage="totalPage" />
  </div>
</template>

<script>
import ShowrrelList from "@/components/showrrel/ShowrrelList";
import { getBlogListByTagName } from "@/api/tag";

export default {
  name: "Tag",
  components: { ShowrrelList },
  data() {
    return {
      blogList: [],
      totalPage: 0,
    };
  },
  watch: {
    //the current components are reused, the blog list is to be re-acquired.
    "$route.fullPath"() {
      if (this.$route.name === "tag") {
        this.getBlogList();
      }
    },
  },
  created() {
    this.getBlogList();
  },
  computed: {
    tagName() {
      return this.$route.params.name;
    },
  },
  methods: {
    getBlogList(pageNum) {
      getBlogListByTagName(this.tagName, pageNum)
        .then((res) => {
          if (res.code === 200) {
            this.blogList = res.data.list;
            this.totalPage = res.data.totalPage;
            this.$nextTick(() => {
              Prism.highlightAll();
            });
          } else {
            this.msgError(res.msg);
          }
        })
        .catch(() => {
          this.msgError("Request failed");
        });
    },
  },
};
</script>

<style scoped></style>
