<template>
  <div class="ui bottom" style="text-align: center">
    <el-pagination
      @current-change="handleCurrentChange"
      :current-page="pageNum"
      :page-count="totalPage"
      layout="prev, pager, next"
      background
      hide-on-single-page
    >
    </el-pagination>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "Pagination",
  props: {
    getBlogList: {
      type: Function,
      required: true,
    },
    totalPage: {
      type: Number,
      required: true,
    },
  },
  //At present, there is only the home page being cached, so this hook will only be called by the home page.
  activated() {
    this.$nextTick(() => {
      if (!this.isBlogToHome) {
        //When routed from other pages to the home page, let the home page assembly page number reset to the first page
        this.pageNum = 1;
      }
    });
  },
  computed: {
    ...mapState(["isBlogToHome", "clientSize"]),
  },
  data() {
    return {
      pageNum: 1,
    };
  },
  methods: {
    //Listening to page number change event
    handleCurrentChange(newPage) {
      //If it is the homepage, scroll to the bottom of Header
      if (this.$route.name === "home") {
        window.scrollTo({ top: this.clientSize.clientHeight, behavior: "smooth" });
      } else {
        //Other pages ( and tab) scroll to the top
        this.scrollToTop();
      }
      this.pageNum = newPage;
      this.getBlogList(newPage);
    },
  },
};
</script>

<style>
.el-pagination.is-background .btn-next,
.el-pagination.is-background .btn-prev,
.el-pagination.is-background .el-pager li {
  background-color: #ffffff !important;
}

.el-pagination.is-background .el-pager li:not(.disabled).active {
  background-color: #409eff !important;
}
</style>
