<template>
  <!--Private Showreel password dialog -->
  <el-dialog
    title="Please enter the protected Showreel password"
    width="30%"
    :visible.sync="blogPasswordDialogVisible"
    :lock-scroll="false"
    :before-close="blogPasswordDialogClosed"
  >
    <el-form
      :model="blogPasswordForm"
      :rules="formRules"
      ref="formRef"
      label-width="80px"
    >
      <el-form-item label="Password" prop="password">
        <el-input v-model="blogPasswordForm.password"></el-input>
      </el-form-item>
    </el-form>
    <span slot="footer">
      <el-button @click="blogPasswordDialogClosed">Cancel</el-button>
      <el-button type="primary" @click="submitBlogPassword">Save</el-button>
    </span>
  </el-dialog>
</template>

<script>
import { mapState } from "vuex";
import { SET_BLOG_PASSWORD_DIALOG_VISIBLE } from "../../store/mutations-types";
import { checkBlogPassword } from "@/api/blog";

export default {
  name: "BlogPasswordDialog",
  computed: {
    ...mapState(["blogPasswordDialogVisible", "blogPasswordForm"]),
  },
  data() {
    return {
      formRules: {
        password: [
          { required: true, message: "Please enter the password", trigger: "change" },
        ],
      },
    };
  },
  methods: {
    blogPasswordDialogClosed() {
      this.$refs.formRef.resetFields();
      this.$store.commit(SET_BLOG_PASSWORD_DIALOG_VISIBLE, false);
    },
    submitBlogPassword() {
      this.$refs.formRef.validate((valid) => {
        if (valid) {
          checkBlogPassword(this.blogPasswordForm)
            .then((res) => {
              if (res.code === 200) {
                this.msgSuccess(res.msg);
                window.localStorage.setItem(
                  `blog${this.blogPasswordForm.blogId}`,
                  res.data
                );
                this.$router.push(`/blog/${this.blogPasswordForm.blogId}`);
                this.blogPasswordDialogClosed();
              } else {
                this.msgError(res.msg);
              }
            })
            .catch(() => {
              this.msgError("Request failed");
            });
        }
      });
    },
  },
};
</script>

<style scoped></style>
