<script>
import { getArchives } from "@/api/archives";

export default {
  name: "Showreel",
  data() {
    return {
      playerVars: {
        autoplay: 1,
      },
      blogMap: {},
      count: 0,
      colorObj: {
        0: "tl-blue",
        1: "tl-dark",
        2: "tl-green",
        3: "tl-purple",
        4: "tl-red",
      },
    };
  },
  created() {
    this.getArchives();
  },
  methods: {
    getArchives() {
      getArchives()
        .then((res) => {
          if (res.code === 200) {
            this.blogMap = res.data.blogMap;
            this.count = res.data.count;
          } else {
            this.msgError(res.msg);
          }
        })
        .catch(() => {
          this.msgError("Request failed");
        });
    },
    toBlog(blog) {
      this.$store.dispatch("goBlogPage", blog);
    },
  },
};
</script>

