<template>
  <header ref="header">
    <div class="view">
      <img
        ref="imgbg1"
        src="https://cdn.jsdelivr.net/gh/ddthien-coder/e_digital/2.jpg"
        style="display: none"
      />
      <div
        class="bg1"
        style="
          background-image: url('https://cdn.jsdelivr.net/gh/ddthien-coder/e_digital/2.jpg');
        "
      ></div>
      <div
        class="bg2"
        style="
          background-image: url('https://cdn.jsdelivr.net/gh/ddthien-coder/e_digital/9.jpg');
        "
      ></div>
      <div
        class="bg3"
        style="
          background-image: url('https://cdn.jsdelivr.net/gh/ddthien-coder/e_digital/8.jpg');
        "
        v-show="loaded"
      ></div>
    </div>
    <div class="text-malfunction">
       <vue-typer
        :text='["Things we can do","ROTO/KEYING, REMOVE ","TRACKING, MATCHMOVE", 
        "COMPOSITING","If that is what you require","Please contact us right away."]'
        :repeat='Infinity'
        :shuffle='false'
        initial-action='typing'
        :pre-type-delay='70'
        :type-delay='70'
        :pre-erase-delay='1000'
        :erase-delay='250'
        erase-style='clear'
        :erase-on-complete='false'
        caret-animation='blink'
      ></vue-typer>
    </div>
    <div class="wrapper">
      <i class="ali-iconfont icon-down" @click="scrollToMain"></i>
    </div>
    <div class="wave1"></div>
    <div class="wave2"></div>
  </header>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "Header",
  data() {
    return {
      loaded: false,
    };
  },
  computed: {
    ...mapState(["clientSize"]),
  },
  watch: {
    "clientSize.clientHeight"() {
      this.setHeaderHeight();
    },
  },
  mounted() {
    this.$refs.imgbg1.onload = () => {
      this.loaded = true;
    };
    this.setHeaderHeight();
    let startingPoint;
    const header = this.$refs.header;
    header.addEventListener("mouseenter", (e) => {
      startingPoint = e.clientX;
    });
    header.addEventListener("mouseout", (e) => {
      header.classList.remove("moving");
      header.style.setProperty("--percentage", 0.5);
    });
    header.addEventListener("mousemove", (e) => {
      let percentage = (e.clientX - startingPoint) / window.outerWidth + 0.5;
      header.style.setProperty("--percentage", percentage);
      header.classList.add("moving");
    });
  },
  methods: {
    //Dynamically change the size of the first image according to the height of the visible window
    setHeaderHeight() {
      this.$refs.header.style.height = this.clientSize.clientHeight + "px";
    },
    //Smooth scroll to the body part
    scrollToMain() {
      window.scrollTo({ top: this.clientSize.clientHeight, behavior: "smooth" });
    },
  },
};
</script>

