<template>
  <div></div>
</template>

<script>
import { getBlogList } from "@/api/home";
import { SET_IS_BLOG_TO_HOME } from "../../store/mutations-types";

export default {
  name: "Home",
  data() {
    return {
      blogList: [],
      totalPage: 0,
      getBlogListFinish: false,
    };
  },
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      if (from.name !== "blog") {
        vm.$store.commit(SET_IS_BLOG_TO_HOME, false);
        vm.getBlogList();
      } else {
        if (!vm.getBlogListFinish) {
          vm.getBlogList();
        }
        vm.$store.commit(SET_IS_BLOG_TO_HOME, true);
      }
    });
  },
  methods: {
    getBlogList(pageNum) {
      getBlogList(pageNum)
        .then((res) => {
          if (res.code === 200) {
            this.blogList = res.data.list;
            this.totalPage = res.data.totalPage;
            this.$nextTick(() => {
              Prism.highlightAll();
            });
            this.getBlogListFinish = true;
          } else {
            this.msgError(res.msg);
          }
        })
        .catch(() => {
          this.msgError("Request fail");
        });
    },
  },
};
</script>

<style scoped></style>
