<template>
  <div class="site">
    <Nav :bName="siteInfo.bName" :categoryList="categoryList" />
    <div class="m-mobile-hide">
      <Header v-if="$route.name === 'home'" />
    </div>

    <div class="main">
        <div class="ui container">
          <div class="ui stackable grid">
            <div class="three wide column m-mobile-hide"></div>
            <div class="ten wide column">
              <keep-alive include="Home">
                <router-view />
              </keep-alive>
            </div>
          </div>
      </div>
    </div>

    <BlogPasswordDialog />
    <el-backtop style="box-shadow: none; background: none">
      <img src="https://cdn.jsdelivr.net/gh/ddthien-coder/e_digital/paper-plane.png" style="width: 40px; height: 40px" />
    </el-backtop>
    <Footer :siteInfo="siteInfo" :badges="badges" :newBlogList="newBlogList" />
  </div>
</template>

<script>
import { getSite } from "@/api/index";
import Nav from "@/components/index/Nav";
import Header from "@/components/index/Header";
import Footer from "@/components/index/Footer";
import Tags from "@/components/sidebar/Tags";
import RandomBlog from "@/components/sidebar/RandomBlog";
import BlogPasswordDialog from "@/components/index/BlogPasswordDialog";
import { mapState } from "vuex";
import {
  SAVE_CLIENT_SIZE,
  SAVE_INTRODUCTION,
  SAVE_SITE_INFO,
  RESTORE_COMMENT_FORM,
} from "@/store/mutations-types";

export default {
  name: "Index",
  components: {
    Header,
    BlogPasswordDialog,
    RandomBlog,
    Tags,
    Nav,
    Footer
  },
  data() {
    return {
      siteInfo: {
        bName: "",
      },
      categoryList: [],
      tagList: [],
      randomBlogList: [],
      badges: [],
      newBlogList: [],
    };
  },
  computed: {
    ...mapState(["focusMode"]),
  },
  watch: {
    "$route.path"() {
      this.scrollToTop();
    },
  },
  created() {
    this.getSite();
    //Review information from LocalStorage recovery
    this.$store.commit(RESTORE_COMMENT_FORM);
  },
  mounted() {
    //Save the visual window size
    this.$store.commit(SAVE_CLIENT_SIZE, {
      clientHeight: document.body.clientHeight,
      clientWidth: document.body.clientWidth,
    });
    window.onresize = () => {
      this.$store.commit(SAVE_CLIENT_SIZE, {
        clientHeight: document.body.clientHeight,
        clientWidth: document.body.clientWidth,
      });
    };
  },
  methods: {
    getSite() {
      getSite().then((res) => {
        if (res.code === 200) {
          this.siteInfo = res.data.siteInfo;
          this.badges = res.data.badges;
          this.newBlogList = res.data.newBlogList;
          this.categoryList = res.data.categoryList;
          this.tagList = res.data.tagList;
          this.randomBlogList = res.data.randomBlogList;
          this.$store.commit(SAVE_SITE_INFO, this.siteInfo);
          this.$store.commit(SAVE_INTRODUCTION, res.data.introduction);
          document.title = this.$route.meta.title + this.siteInfo.webTitleSuffix;
        }
      });
    },
  },
};
</script>

<style scoped>
.site {
  display: flex;
  min-height: 100vh; /* there is no element, you also open the page to 100% */
  flex-direction: column;
}

.main {
  margin-top: 40px;
  flex: 1;
}

.main .ui.container {
  width: 1400px !important;
  margin-left: auto !important;
  margin-right: auto !important;
}

.ui.grid .three.column {
  padding: 0;
}

.ui.grid .ten.column {
  padding-top: 0;
}

.m-display-none {
  display: none !important;
}
</style>
