<template>
  <footer class="ui inverted segment padded-tb-learg opacity">
    <div class="ui center aligned container">
      <div class="ui inverted divided stackable grid">
        <div class="three wide column">
          <div class="ui link list">
            <h4 class="ui inverted header text-thin text-spaced">
              {{ siteInfo.footerImgTitle }}
            </h4>
            <div class="item">
              <img
                :src="siteInfo.footerImgUrl"
                class="ui rounded image"
                alt=""
                style="width: 200px"
              />
            </div>
            <a href="https://www.facebook.com/Digital-B-103697511164259" target="_blank"><i class="facebook icon large"></i></a>
            <a href="https://www.linkedin.com/in/b-digital-04033118b/" target="_blank"> <i class="linkedin icon large"></i></a>
          </div>
        </div>

        <div class="six wide column">
          <h4 class="ui inverted header text-thin text-spaced">Showreel</h4>
          <div class="ui inverted link list">
            <a
              href="javascript:;"
              @click.prevent="toBlog(item)"
              v-for="item in newBlogList"
              :key="item.id"
              class="item text-thin padded-tb-small"
              >{{ item.title }}</a
            >
          </div>
        </div>

        <div class="seven wide column">
          <h4 class="ui inverted header text-thin text-spaced">Contact</h4>
            <h4 class="text-200" style="text-align: center">
              <span v-html="about.content"></span>
            </h4>
        </div>
      </div>

      <div class="ui divider"></div>

      <p class="text-thin m-text-spaced opacity-tiny">
        <span style="margin-right: 10px" v-if="siteInfo.copyright">{{
          siteInfo.copyright.title
        }}</span>
        <router-link to="/" style="color: #ffe500" v-if="siteInfo.copyright">{{
          siteInfo.copyright.siteName
        }}</router-link>
        <span style="margin: 0 15px" v-if="siteInfo.copyright && siteInfo.beian">|</span>
      </p>

      <div class="github-badge" v-for="(item, index) in badges" :key="index">
        <a
          rel="external nofollow noopener"
          :href="item.url"
          target="_blank"
          :title="item.title"
        >
          <span class="badge-subject">{{ item.subject }}</span>
          <span class="badge-value" :class="`bg-${item.color}`">{{ item.value }}</span>
        </a>
      </div>
    </div>
  </footer>
</template>

<script>
import { getAbout } from "@/api/about";
export default {
  name: "Footer",
  props: {
    siteInfo: {
      type: Object,
      required: true,
    },
    badges: {
      type: Array,
      required: true,
    },
    newBlogList: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      about: {
          title: "",
          musicId: "",
          content: "",
          commentEnabled: "false",
        },
    }
  },
  created() {
    this.getData();
  },
  methods: {
    toBlog(blog) {
      this.$store.dispatch("goBlogPage", blog);
    },
    getData() {
      getAbout()
        .then((res) => {
          if (res.code === 200) {
            this.about = res.data;
          } else {
            this.msgError(res.msg);
          }
        })
        .catch(() => {
          this.msgError("Request failed");
        });
    },
  },
};
</script>

<style scoped>
@import "../../assets/css/badge.css";

.github-badge a {
  color: #fff;
}

.beian {
  width: 17px;
  height: 17px;
  margin-bottom: -4px;
  margin-right: 5px;
}
</style>
