<script>
import { getAbout } from "@/api/about";

export default {
  name: "Contact",
  data() {
    return {
      about: {
        title: "",
        musicId: "",
        content: "",
        commentEnabled: "false",
      },
    };
  },
  created() {
    this.getData();
  },
  methods: {
    getData() {
      getAbout()
        .then((res) => {
          if (res.code === 200) {
            this.about = res.data;
          } else {
            this.msgError(res.msg);
          }
        })
        .catch(() => {
          this.msgError("Request failed");
        });
    },
  },
};
</script>

